<template>
  <div id="selfInfo" v-loading="loading" element-loading-background="#f8f8f8">
    <el-row>
      <h2>头像</h2>
      <el-divider></el-divider>
      <el-image
        style="width: 300px; height: 300px; border-radius: 50%; margin: 10px 0"
        :src="userForm.usrAvatar"
        :preview-src-list="[userForm.usrAvatar]"
      >
      </el-image>
    </el-row>
    <el-row>
      <h2>基本信息</h2>
      <el-divider></el-divider>
    </el-row>
    <div
      style="
        display: flex;
        justify-content: start;
        align-items: start;
        flex-wrap: wrap;
      "
    >
      <div style="display: flex; justify-content: start; align-items: start">
        <div class="card row">
          <i class="el-icon-s-custom account"></i>
          <div class="col">
            <strong>账号</strong>
            <p>{{ userForm.usrAccount }}</p>
          </div>
        </div>
      </div>
      <div style="display: flex; justify-content: start; align-items: start">
        <div class="card row">
          <i class="el-icon-phone phone"></i>
          <div class="col">
            <strong>电话</strong>
            <p>{{ userForm.usrPhone }}</p>
          </div>
        </div>
      </div>
      <div style="display: flex; justify-content: start; align-items: start">
        <div class="card row">
          <i class="el-icon-message email"></i>
          <div class="col">
            <strong>邮箱</strong>
            <p>{{ userForm.usrEmail }}</p>
          </div>
        </div>
      </div>
      <div style="display: flex; justify-content: start; align-items: start">
        <div class="card row">
          <i class="el-icon-trophy level"></i>
          <div class="col">
            <strong>身份</strong>
            <p>{{ userForm.usrVipLevel }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      loading: true,
    };
  },
  computed: {
    ...mapState("user", ["userForm"]),
  },
  methods: {
    ...mapActions("login", { getInfo: "getInfo" }),
  },
  activated() {
    console.log("activated");
    this.getInfo(this);
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
  },
};
</script>

<style scoped>
#selfInfo {
  padding: 30px 25px;
}
h3 {
  box-shadow: inset 5px 0px 0 0 rgb(28, 173, 240);
  padding: 0 10px;
  margin: 10px 0;
}

i {
  margin-right: 10px;
  font-size: 50px;
  transition: all 0.3s;
}
.card {
  margin: 20px;
  width: 250px;
  height: 100px;
  padding: 0px 10px;
  border: 1px solid #b9b6b6;
  border-radius: 5px;
  transition: all 0.3s;
}
.card:hover {
  box-shadow: 0 0 10px 0 #b9b6b6;
}
.row {
  display: flex;
  justify-content: start;
  align-items: center;
}
.col {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.account {
  color: #51cee8;
  transition: all 0.3s;
}
.account:hover {
  color: white;
  background-color: #51cee8;
}
.email {
  color: #f4e914;
  transition: all 0.3s;
}
.email:hover {
  color: white;
  background-color: #f4e914;
}
.phone {
  color: #13f3b0;
  transition: all 0.3s;
}
.phone:hover {
  color: white;
  background-color: #13f3b0;
}
.level {
  color: #f25962;
  transition: all 0.3s;
}
.level:hover {
  color: white;
  background-color: #f25962;
}
</style>
